.expiriance {
  background-color: $white;
  border-radius: 30px;
  padding-block: 72px;

  @include onLaptop{
    padding-block: 130px;
  }

  &__content{
    @include pageGrid;

  }

  &__title {
    grid-column: 1 / -1;
    color: #253757;
    text-align: center;
    margin-bottom: 56px;
    @include onTablet{
      margin-bottom: 64px;
    }
  }
}
